@import url("https://fonts.googleapis.com/css2?family=Noto+Serif+TC:wght@200;300;400;500;600;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Noto+Serif+Display:ital,wght@1,400&display=swap");
@font-face {
  font-family: "Metric";
  src: url("../../fonts/Metric-Regular.woff2") format("woff2");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "Metric";
  src: url("../../fonts/Metric-Thin.woff2") format("woff2");
  font-weight: 100;
  font-style: normal;
}
@font-face {
  font-family: "Metric";
  src: url("../../fonts/Metric-Medium.woff2") format("woff2");
  font-weight: 500;
  font-style: normal;
}
@font-face {
  font-family: "Metric";
  src: url("../../fonts/Metric-Bold.woff2") format("woff2");
  font-weight: 900;
  font-style: normal;
}
@font-face {
  font-family: "Metric";
  src: url("../../fonts/Metric-SemiBold.woff2") format("woff2");
  font-weight: 600;
  font-style: normal;
}
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font: inherit;
  vertical-align: baseline;
  text-decoration: none;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

* {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

img {
  max-width: 100%;
  width: 100%;
}

body {
  background-color: #fff;
}

.wrapper {
  padding: 0 2.6rem;
}

label {
  font-size: 0.75rem;
  line-height: 2.5rem;
  font-family: "helvetica", "arial", sans-serif;
  letter-spacing: 0.5px;
  color: red;
}

header {
  margin-top: 1.6rem;
}
header h1 {
  font-size: 2.4rem;
  line-height: 1.4;
  font-family: "Noto Serif TC", serif;
  font-weight: 400;
}

.works {
  margin: 8rem 0 2rem 0;
}
.works .works-header {
  display: grid;
  grid-template-columns: 1fr 4fr 5fr;
}
.works .works-list .works-work {
  display: grid;
  grid-template-columns: 1fr 4fr 5fr;
  border-top: 1px solid #444;
  padding: 1.2rem 0 1.2rem 0;
  font-size: 1.7rem;
  font-family: "Noto Serif TC", serif;
  font-weight: 300;
}
.works .works-list .works-work:hover {
  font-family: "Noto Serif Display", serif;
  font-weight: 400;
  font-style: italic;
}

.cv, .about {
  margin: 6rem 0 0 0;
}
.cv h2, .about h2 {
  font-size: 1.7rem;
  line-height: 2.25rem;
  font-family: "Noto Serif TC", serif;
  font-weight: normal;
}
.cv a, .about a {
  color: #444;
  text-decoration: underline;
}

.about label:last-child {
  color: #444 !important;
}